import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {SellerUrlFinance} from 'src/app/core/utils/URL/seller-url/seller-finance/seller-url-finanace';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {FinanceOverviewResponse} from 'src/app/core/models/seller/finance/response/FinanceOverviewResponse';
import {WillPayFinanceOrderFinance} from 'src/app/core/models/seller/finance/response/WillPayFinanceOrderFinance';
import {PaidFinanceOrderFinance} from 'src/app/core/models/seller/finance/response/PaidFinanceOrderFinance';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';

@Injectable({
	providedIn: 'root'
})
export class SellerFinanceService {
	constructor(private sellerUrlFinance: SellerUrlFinance, private http: HttpClient) {}
	getFinanceOverview(): Observable<ApiResult<FinanceOverviewResponse>> {
		return this.http.get<ApiResult<FinanceOverviewResponse>>(this.sellerUrlFinance.sellerGetFinanceOverview());
	}
	getPaidFinance(request: PagingRequestBase): Observable<ApiResult<PagedResult<PaidFinanceOrderFinance>>> {
		return this.http.get<ApiResult<PagedResult<PaidFinanceOrderFinance>>>(
			this.sellerUrlFinance.sellerGetPaidFinance(request)
		);
	}
	getWillPayFinance(request: PagingRequestBase): Observable<ApiResult<PagedResult<WillPayFinanceOrderFinance>>> {
		return this.http.get<ApiResult<PagedResult<WillPayFinanceOrderFinance>>>(
			this.sellerUrlFinance.sellerGetWillPayFinance(request)
		);
	}
}
