import {SellerFinanceService} from './../../../../../../../../core/services/seller/seller-finance/seller-finance.service';
import {Component, OnInit} from '@angular/core';
import {PaidFinanceOrderFinance} from 'src/app/core/models/seller/finance/response/PaidFinanceOrderFinance';
import {WillPayFinanceOrderFinance} from 'src/app/core/models/seller/finance/response/WillPayFinanceOrderFinance';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';

@Component({
	selector: 'app-finance-detail',
	templateUrl: './finance-detail.component.html',
	styleUrls: ['./finance-detail.component.less']
})
export class FinanceDetailComponent implements OnInit {
	loading = true;
	isWillPayLoading=true;
	isPaidLoading=true;
	pageIndex = 1;
	tabIndex = 0;
	paidFinanceOrderFinance: PagedResult<PaidFinanceOrderFinance>;
	willPayFinanceOrderFinance: PagedResult<WillPayFinanceOrderFinance>;
	constructor(
		private destroy$: DestroyService,
		private notificationService: NotificationService,
		private sellerFinanceService: SellerFinanceService
	) {}

	ngOnInit(): void {
		this.getPaidFinanceOrderFinance(this.pageIndex);
		this.getWillPayFinance(this.pageIndex);
	}
	changeTab(tabIndex: number) {
		this.tabIndex = tabIndex;
		console.log(this.tabIndex);
		this.pageIndex = 1;
		this.isWillPayLoading=true;
		this.isPaidLoading=true;
		this.paidFinanceOrderFinance = {} as PagedResult<PaidFinanceOrderFinance>;
		this.willPayFinanceOrderFinance = {} as PagedResult<WillPayFinanceOrderFinance>;


		if (this.tabIndex == 0) {
			this.getWillPayFinance(this.pageIndex);
		}
		if (this.tabIndex == 1) {
			this.getPaidFinanceOrderFinance(this.pageIndex);
		}
	}
	getPaidFinanceOrderFinance(pageIndex: number) {
		this.isPaidLoading = true;
		this.sellerFinanceService
			.getPaidFinance({pageIndex: pageIndex, pageSize: 10})
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.resultObj) {
						this.paidFinanceOrderFinance = result.resultObj;
						this.isPaidLoading = false;
					}
					this.isPaidLoading = false;
				},
				(error) => {
					this.isPaidLoading = false;
					this.notificationService.showErrorNotification(error.message);
				}
			);
	}
	getWillPayFinance(pageIndex: number) {
		this.isWillPayLoading = true;
		this.sellerFinanceService
			.getWillPayFinance({pageIndex: pageIndex, pageSize: 10})
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.resultObj) {
						this.willPayFinanceOrderFinance = result.resultObj;
						this.isWillPayLoading = false;
					}
					this.isWillPayLoading = false;
				},
				(error) => {
					this.isWillPayLoading = false;
					this.notificationService.showErrorNotification(error.message);
				}
			);
	}
	pageChanged(page: number) {
		this.pageIndex = page; // receive the page number
		console.log(this.pageIndex);
		if (this.tabIndex == 0) {
			this.getWillPayFinance(this.pageIndex);
		}
		if (this.tabIndex == 1) {
			this.getPaidFinanceOrderFinance(this.pageIndex);
		}
	}
}
