import { SellerUrlBase } from './../seller-url-base';
import {HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlFinance extends SellerUrlBase {
	constructor() {
		super();
	}
	sellerGetFinanceOverview() {
		return this.sellerUrl + '/finance/overview';
	}
	sellerGetPaidFinance(request:PagingRequestBase) {
		return this.sellerUrl + `/finance/paid-order?pageIndex=${request.pageIndex}&pageSize=${request.pageSize}`;
	}
	sellerGetWillPayFinance(request:PagingRequestBase) {
		return this.sellerUrl + `/finance/will-pay?pageIndex=${request.pageIndex}&pageSize=${request.pageSize}`;
	}
	
}
