import {Component, OnInit} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {FinanceOverviewResponse} from 'src/app/core/models/seller/finance/response/FinanceOverviewResponse';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SellerFinanceService} from 'src/app/core/services/seller/seller-finance/seller-finance.service';

@Component({
	selector: 'app-finance-overview',
	templateUrl: './finance-overview.component.html',
	styleUrls: ['./finance-overview.component.less'],
	providers: [DestroyService]
})
export class FinanceOverviewComponent implements OnInit {
	isLoading = true; //skelton
	financeOverviewResponse: FinanceOverviewResponse;
	constructor(
		private notificationService: NotificationService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private sellerFinanceService: SellerFinanceService
	) {}
	ngOnInit(): void {
		this.GetFinanceOverview();
	}
	GetFinanceOverview() {
		this.isLoading = true;
		this.sellerFinanceService
			.getFinanceOverview()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.isSuccessed) {
						if (result.resultObj) {
							this.financeOverviewResponse = result.resultObj;
							console.log(this.financeOverviewResponse);
							this.isLoading = false;
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
					this.isLoading = false;
				},
				(error) => {
					this.isLoading = false;
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
}
